@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

$color-gray-light: rgba($color-gray-2, 0.1);

.guide-step {
	&__sticky-header {
		position: sticky;
		top: 0rem;
		z-index: 3;
		&.--mobile{
			top: 6.4rem;
		}

		height: 4.6rem;

		display: flex;
		align-items: center;

		background-color: $color-gray-1;
		border-bottom: 1px solid $color-gray-light;

		color: $color-purple;
		font-size: 1.3rem;
		font-weight: 600;

		& > i {
			display: inline-block;
			width: 1.8rem;
			height: 1.8rem;
			border-radius: 50%;
			margin-left: 0.5rem;
			margin-right: 1rem;
		}
	}
	& > main {
		padding: 2.4rem 0;
		color: $color-gray-3;

		& > h1 {
			margin-bottom: 2.4rem;

			font-size: 1.7rem;
			line-height: 120%;
			word-break: break-word;
		}

		& > p {
			margin-bottom: 2rem;

			font-size: 1.5rem;
			line-height: 155%;
			white-space: pre-wrap;
			word-break: break-word;
		}
	}
	&__image-container {
		position: relative;
		height: 26rem;
		margin-bottom: 2.4rem;

		overflow: hidden;

		border-radius: 0.8rem;
		background-color: $color-gray-light;

		& .annotation-image__placeholder {
			fill: $color-white;
			transform: scale(1.6);
		}

		& .loading-wheel {
			transform: scale(0.5);
			opacity: 0.4;
		}
	}

	.pdf-sign {
		display: flex;
		position: absolute;
		width: 44px;
		height: 24px;
		right: 10px;
		top: 8px;
		margin: 2px;
		font-family: Silka;
		font-style: normal;
		font-weight: 600;
		font-size: 12px;
		line-height: 120%;
		background: #c43f3f;
		border-radius: 4px;
		text-align: center;
		color: #ffffff;
		justify-content: center;
		align-items: center;
		z-index: 1;
	}
}
