@import "../../../styles/variables.scss";

.text-content {
	position: relative;

	margin-bottom: 1.3rem;
	margin-left: 1.6rem;

	font-size: 1.5rem;
	line-height: 155%;

	overflow: visible;

	&::before {
		content: "";

		display: block;

		position: absolute;
		z-index: 0;
		left: -1.5rem;
		top: 0.8rem;

		width: 0.6rem;
		height: 0.6rem;

		border-radius: 50%;
		background-color: $color-gray-2;
	}
}

.warning-content {
	position: relative;

	margin-top: 2.2rem;
	margin-bottom: 1.6rem;
	padding: 1.4rem 1.6rem 1.2rem;

	border-radius: 0.6rem;
	background-color: rgba($color-warning, 0.1);

	color: $color-warning;
	font-size: 1.4rem;
	font-weight: 500;

	&::before {
		content: "WARNING";

		position: absolute;
		top: -1rem;

		padding: 0.2rem 0.4rem;

		background-color: $color-warning;
		border-radius: 0.2rem;

		color: $color-white;
		font-size: 1.3rem;
	}
}

.tip-content {
	position: relative;

	margin-top: 2.2rem;
	margin-bottom: 1.6rem;
	padding: 1.4rem 1.6rem 1.2rem;

	border-radius: 0.6rem;
	background-color: rgba($color-purple, 0.1);

	color: $color-purple;
	font-size: 1.4rem;
	font-weight: 500;

	&::before {
		content: "TIP";

		position: absolute;
		top: -1rem;

		padding: 0.2rem 0.4rem;

		background-color: $color-purple;
		border-radius: 0.2rem;

		color: $color-white;
		font-size: 1.3rem;
	}
}
