@import "../../styles/variables.scss";
@import "../../styles/mixins.scss";

.guide-player {
	&__image-container {
		height: 35vh;

		display: flex;
		align-items: center;
		justify-content: center;

		overflow: hidden;

		& .annotation-image__placeholder {
			fill: $color-white;
			transform: scale(2);
		}

		& .loading-wheel {
			transform: scale(0.5);
			opacity: 0.4;
		}
	}

	&__author-image {
		-webkit-transform: translate3d(0, 0, 0);
		display: inline-block;
		height: 3.2rem;
		width: 3.2rem;
		min-width: 3.2rem;
		min-height: 3.2rem;
		border-radius: 50%;
		overflow: hidden;
	}

	&__author-content {
		display: flex;
		align-items: center;
		margin-bottom: 1.6rem;
	}

	&__author-name {
		font-weight: 600;
	}

	&__autohor-label {
		color: #7f86a0;
	}

	&__author-text-content {
		display: flex;
		flex-direction: column;
		margin-left: 0.8rem;
	}

	&__information {
		padding: 3.2rem 1.6rem;

		background-color: $color-gray-3;
		color: $color-white;

		& > h2 {
			margin-bottom: 0.6rem;

			color: $color-gray-1;
			font-size: 1.5rem;
			line-height: 130%;
			word-break: break-all;
		}

		& > h1 {
			margin-bottom: 1.6rem;

			max-width: 80%;

			font-size: 2.8rem;
			line-height: 120%;
			word-break: break-word;
		}
		& > span {
			display: flex;
			align-items: center;

			height: 1.5rem;

			color: $color-gray-2;
			font-size: 1.3rem;
			line-height: 100%;

			& > div {
				margin-right: 0.4rem;
				margin-top: 0.1rem;
			}
		}
	}
	&__main {
		padding: 3.2rem 1.6rem;
		color: $color-gray-3;

		& > h1 {
			margin-bottom: 1.6rem;

			font-size: 1.9rem;
			line-height: 120%;
			word-break: break-all;
		}

		& > p {
			margin-bottom: 3.5rem;

			font-size: 1.5rem;
			line-height: 155%;
			white-space: pre-wrap;
			word-break: break-word;
		}
	}
}
