.pdf-wrapper {
	position: relative;
	box-sizing: content-box;

	.container {
		box-sizing: content-box;
		position: absolute;
		overflow: auto;
		width: 100%;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #7F86A0;;

		div {
			box-sizing: content-box;
		}
	}
}

canvas {
	margin: auto;
	display: block;
	width: 100%;
}
